.Experience{
    /* background: #000; */
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 7rem;
    height: 30rem;
    margin-bottom: 8rem;
}
.Achievements{
    /* background: #000; */
    display: flex;
    flex-direction: column;
    align-items: center;

}
.circle{
    width: 4rem;
    height: 4rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}
.circle::before{
    border-radius: 100%;
    content: ' ';
    background-image: linear-gradient(to  bottom ,rgb(242, 255, 3) 0%,rgb(179, 20, 141) 100%);
    top: -8px;
    bottom: -8px;
    left: -8px;
    right: -8px;
    position: absolute;
    z-index: -1;
   box-shadow: var(--boxShadow);
}
.Achievements span:nth-child(3){
    color: var(--orange);
}
@media screen and (max-width: 480px){
.Achievements{
    margin: 0 -15px;
}
.mob{
   width: 30px;
}



}