.card{
    width: 14rem;
    height: 17rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    text-align: center;
    background: rgba(255, 244, 329,291);
    border: 5px solid var(--orange);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}
.card img{
    transform: scale(0.6);
    margin-bottom:-2rem ;
}

.card>:nth-child(3){
    color: var(--gray);
    font-size: 16px;
}
.c-btn{
    background: #ffffff7f;
    color: rgb(37, 37, 123);
}
@media screen and (max-width: 480px){
    .card{
        width: auto;
        /* gap: 4rem; */
    }

}

