.Footer{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;

}
.f-content{
    position: absolute;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 4rem;
    gap: 4rem;
    color: white;
}
.f-icon{
    display: flex;
    gap: 3rem;
}
@media screen and (max-width: 480px){
/* .Footer{
    position: static;
} */
.f-content{
    justify-content: center;
    align-items: center;
    margin: 3px;
    
}
}