.works
{
    padding: 0 3rem 0 3rem;
    display: flex;
    margin-top: 4rem;
    height: 90vh;
}
.w-right{
   position: relative;
}
.w-main-circle{
    position: relative;
    top: 2rem;
    left: 9rem;
    width: 18rem;
    height: 18rem;
    background: white;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
}
.w-main-circle > *{
    position: relative;
    /* background-color: aqua; */
}
.w-main-circle > :nth-child(1){
    top: -3rem;
    left: 6rem;
    /* background-color: rgb(210, 48, 8); */
}
.w-main-circle > :nth-child(2){
    top: 5rem;
    left: -4rem;
    /* background-color: rgb(51, 255, 10); */
}
.w-main-circle > :nth-child(3){
    top: 6rem;
    left: 5rem;
    /* background-color: rgb(51, 255, 10); */
}
.w-main-circle > :nth-child(4){
    top: 15rem;
    left: 5rem;
    /* background-color: rgb(51, 255, 10); */
}
.w-main-circle > :nth-child(5){
    top: 5rem;
    left: 15rem;
    /* background-color: rgb(51, 255, 10); */
}
.w-sec-circle{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border: 5px solid #e4e4e4;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: whitesmoke;
    box-shadow: var(--smboxShadow);
}
.w-sec-circle>img{
    transform: scale(0.6);
}
.w-back-circle{
    position: absolute;
    border-radius: 100%;
    height: 13rem;
    width: 13rem;
    z-index: -1;
}
.blue-circle{
    background: #ff00ff;
    left: 18rem;
    top: 0rem;
}
.yllow-circle{
    background: #f3f310;
    left: 18rem;
    top: 8rem;

}
@media screen and (max-width: 480px){
    .works{
        flex-direction: column;
        height: 55rem;
        padding: 0;
        gap :5rem;
    }
    .w-right{
        display: flex;
        flex-direction: column;
        justify-content: start;
        transform: scale(0.8);
    }
    .w-main-circle{
        position: static;
    }
    .w-back-circle{
        left: 8rem !important;
    }
}

