.Services{
    /* background-color: aqua; */
    padding: 0 3rme 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 13rem;
    margin-top:9rem ;
}
.awesome{
    display: flex;
    flex-direction: column;
    position: relative;
    /* background-color: aqua; */
}
.awesome>:nth-child(1){
    color: var(--black);
    font-size:2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(2){
    color: var(--orange);
    font-size:4rem;
    margin-top:  0.5rem;
    margin-left:  2rem;
    font-weight:bold;
}
.awesome>:nth-child(3){
    color: var(--gray);
    margin-top:  1rem;
    font-size:2.5rem;
    /* font-weight: bold; */
}
.s-btn{
    width: 10rem;
    height: 3rem;
    margin-top: 1rem;
}

