.Cards{
    position: relative;
}
.Cards>*{
    position: absolute;
}
.Card1{
    top: 04rem;
    left: 20rem;
}
.Card2{
    top: 16rem;
    left: 0rem;
   
}

.Card3{
    left: 20rem;
    top: 29rem;
   
}
@media screen and (max-width: 480px){
.Services{
    margin-top: 0;
    flex-direction: column;
    gap: 5rem;
    height: 66rem;
    padding: 0;
}
.Cards{
    display: flex;
    flex-direction: column;
    gap: 17rem;
}
.Cards>*{
    position: static;
    gap: 3rem;
}
}

