.n-Wrapper{
    /* background-color: aqua; */
    height: 1 0vh;
    display: flex;
    
    position:absolute;
    z-index: 99;
    justify-content: flex-end;
}
.n-left{
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;
}
.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}
.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-size: 400;
}
.n-list{
    flex: 10;
}
.n-list>ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    list-style-type: none;

}
.n-list>ul :hover{
    cursor: pointer;
    color: var(--orange);
    
}
.n-btn{
   
    flex: 2;

}
@media screen and (max-width: 480px){
.n-list{
    display: none;
}
.n-btn{
    display: none;
}
}

