.indro{
    display: flex;
    height: 77vh;
    /* background-color: aqua; */
    margin-top: 6rem;
}

.i-left{
    
    display: flex;
    position: relative;
    flex: 1;
    flex-direction:column;
    gap:2rem;
}
.i-name{
    display: flex;
    flex-direction: column;

}
.i-name>:nth-child(1){
    color: var(--black);
    font-size: 3rem;
    font-weight: bold;
}
.i-name>:nth-child(2){
    color: var(--orange);
    font-size: 3rem;
    font-weight: bold;
}
.i-name>:nth-child(3){
    color: var(--grey);
    /* font-size: 3rem; */
    font-weight: bold;
    margin-top: 10px;
}
.i-name>:nth-child(3){
color: var(--orange);
}


.i-btn{
   width: 7rem;
   height: 3rem;
   justify-content: center;
   align-items: center;
}
.i-icon{
    display: flex;
    gap: 2;
    margin-top: 3rem;
}
.i-icon>*{
    transform: scale(0.5);
}
.i-icon>*:hover{
    cursor: pointer;
}


.i-right{
    flex:1;
    position: relative;
    /* background-color: aquamarine; */
}
.i-right>*{
    position: absolute;
    z-index: 1;
}
.i-right>:nth-child(1){
   transform: scale(0.67);
   left: -15%;
   top: -9%;
}
.i-right>:nth-child(2){
    transform: scale(0.67);
    left: -3rem;
    top: -4.6rem;
 }
 .i-right>:nth-child(3){
    transform: scale(1.46);
    top: 140px;
    width: 200px;
    height: 250px ;
    border-radius: 2rem;
    left: 30%;
    background: linear-gradient(18deg,#146cb0 26.17%, rgb(12, 114, 182));
    box-shadow: 0px 20px 24px 3px rgba(40, 56, 90, 0.639);  
 }
 .i-right>:nth-child(4){
    transform: scale(0.46);
    top: -72px;
    left: -90px;   
 }
 .i-blur{
    position: absolute;
    width: 22rem;
    height: 20rem;
    border-radius: 50%;
   
    filter: blur(32px);
    z-index: -9;
    top: -59px;
    left: 56%;

 }
 @media screen and (max-width: 480px){
 .indro{
    flex-direction: column;
    height: 64rem;
    gap: 7rem;
 }
 .i-right{
    transform: scale(0.8);
    left: -3rem;
 }
 .i-right .i-blur{
    display: none;
 }
 .floatingDev:nth-of-type(1){
   top:-2rem !important;    
   left: 240px;
}
.floatingDev:nth-of-type(2){
    top:27rem !important;    
    left: -3rem;
 }
 
}


