:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);    
}
.button{
  border-radius: 34px;
  border: none;
  color:white;
  font-size: 16px;
  padding: 11px 26px 11px 26px;
  background: linear-gradient(18deg,#F5C32C 26.17%, rgb(231, 200, 29));
  box-shadow: 0px 20px 24px 3px rgba(252, 167, 40, 0.639);
}
.button:hover{
  color: var(--orange);
  background: white;
  cursor: pointer;
  border: 2px solid var(--orange);
}
@media screen and (max-width: 480px){
  .app{
    padding: 0.5rem 1rem;
  }
  
}
  

  