.toggle{
    display: flex;
    justify-content: space-around;
    border: 3px solid var(--orange);
    border-radius: 1rem;
    position: fixed;
    left: 3px;
    padding: 3px;
    cursor: pointer;
}
.toggle>*{
    width: 1rem;
    height: 1rem;
    color: var(--orange);
}
.t-btn{
    background: var(--orange);
    border-radius: 100%;
    position: absolute;

}