.Portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 33rem;
}
.Portfolio span:nth-child(1){
    color: var(--black);
    font-size: 2rem;
    font-weight: bold;
}
.Portfolio span:nth-child(2){
    color: var(--orange);
    font-size: 2.6rem;
    font-weight: bold;
}
/* slider */
.Portfolio .swiper{
    overflow: visible !important;
}
.Portfolio-slider{
    margin-top: 3rem;
    width: 100%;
}
.Portfolio-slider .swiper-slide{
   
    width: 20rem;
}
.Portfolio img{
    /* margin-top: 3rem; */
    width: 19rem;
    filter: drop-shadow(-12px 15px 13px rgba(0 ,0 ,0 ,0.25));
    border-radius: 19px;
}

@media screen and (max-width: 480px){
    .Portfolio-slider{
        width: 30rem;

    }
}

